function getExpr(objName, attr) {
  if (attr.isVBind()) {
    return objName + `[${ attr.attrValue }]`;
  }
  return objName + '.' + attr.attrValue;
}

export default {
  id: 'el-form-item',
  name: '',
  eventTypes: [],
  type: ['container', 'form-item'],
  slots: [
    {
      slot: 'default',
      name: '',
    },
    {
      slot: 'label',
      name: '',
    },
    {
      slot: 'error',
      name: '',
    },
  ],
  props: {
    prop: {
      label: '',
      editors: 'string',
      change(attr, old, node) {
        let form = node.closest('el-form');
        let input = node.findChild('.input');
        if (!input || !form) return;

        let formModel = form.getAttrValue(':model');
        if (!formModel) return;

        let [vModelConfig] = input.getConfig('vModel', []);
        if (!vModelConfig) return;

        let vModel = 'v-model';
        if (vModelConfig && vModelConfig != 'modelValue') {
          vModel += ':' + vModelConfig;
        }

        let expr = '';
        let propName = attr && attr.attrValue;
        if (propName) {
          expr = getExpr(formModel, attr);
        }

        input.setAttrValue(vModel, expr);
      },
    },
    label: {
      label: '',
      editors: 'string',
    },
    labelWidth: {
      label: '',
      editors: 'string',
    },
    required: {
      label: '',
      editors: 'boolean',
    },
    rules: {
      label: '',
      editors: 'validator',
    },
    error: {
      label: '',
      editors: 'string',
    },
    showMessage: {
      label: '',
      editors: 'boolean?defaultValue=true',
    },
    inlineMessage: {
      label: '',
      editors: 'boolean?trueText=&falseText=',
    },
    size: {
      label: '',
      editors: 'select?@items=medium:|small:|mini:',
    },
  },
};
