export default {
  id: 'el-checkbox-group',
  name: '',
  eventTypes: [
    {
      id: 'change',
      name: '',
    },
  ],
  slots: [
    {
      slot: 'default',
      name: '',
    },
  ],
  type: ['input', 'checkbox-group'],
  props: {
    modelValue: {
      label: '',
    },
    size: {
      label: '',
      editors: 'select?@items=medium:|small:|mini:',
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    min: {
      label: '',
      editors: 'number',
    },
    max: {
      label: '',
      editors: 'number',
    },
    textColor: {
      label: '',
      editors: 'string',
    },
    fill: {
      label: '',
      editors: 'string',
    },
  },

  vModel: ['modelValue'],

  optionEditor: {
    title: '',
    tag: 'el-checkbox',
    getData(node) {
      return {
        value: node.getAttrValue('value'),
        label: node.getText(),
      };
    },
    setData(node, data) {
      node.setAttrValue('value', data.value);
      node.setText(data.label);
    },
  },

  vucProxyOption: {
    onBeforeRender(vucNode, context) {
      vucNode.dfs((node) => {
        context.cancelOutline(node);
      });
    },
  },
};
