export function getFormExplorerComponents() {
  let base = {
    title: '',
    children: [
      {
        title: '',
        node: `<el-button></el-button>`,
      },
      {
        title: 'div',
        node: `<div style="min-height: 20px;"></div>`,
      },
    ],
  };

  let layout = {
    title: '',
    children: [
      {
        title: '',
        node: `<el-row type="flex"></el-row>`,
      },
      {
        title: '',
        node: `<el-col :span="12"></el-col>`,
      },
      {
        title: '',
        node: '<el-card header=""></el-card>',
      },
      {
        title: '',
        node: '<el-tabs model-value="pane1"><el-tab-pane label="1" name="pane1"></el-tab-pane> <el-tab-pane label="2" name="pane2"></el-tab-pane> </el-tabs>',
      },
    ],
  };

  let inputs = [
    {
      title: '',
      tag: 'el-input',
      node: `<el-input></el-input>`,
    },
    {
      title: '',
      tag: 'el-textarea',
      node: `<el-input type="textarea"></el-input>`,
    },
    {
      title: '',
      tag: 'el-select',
      node: `<el-select><el-option label="1">1</el-option><el-option label="2">2</el-option></el-select>`,
    },
    {
      title: '',
      tag: 'el-radio-group',
      node: `<el-radio-group><el-radio label="1">1</el-radio><el-radio label="2">2</el-radio></el-radio-group>`,
    },
    {
      title: '',
      tag: 'el-checkbox-group',
      node: `<el-checkbox-group :model-value="[]"> <el-checkbox label="1">1</el-checkbox> <el-checkbox label="2">2</el-checkbox> </el-checkbox-group>`,
    },
    {
      title: '',
      tag: 'el-autocomplete',
      node: `<el-autocomplete/>`,
    },
    {
      title: '',
      tag: 'el-cascader',
      node: `<el-cascader/>`,
    },
    {
      title: '',
      tag: 'el-date-picker',
      node: `<el-date-picker format="YYYY-MM-DD" valueFormat="YYYY-MM-DD"/>`,
    },
    {
      title: '',
      tag: 'el-input-number',
      node: `<el-input-number/>`,
    },
    {
      title: '',
      tag: 'el-rate',
      node: `<el-rate/>`,
    },
    {
      title: '',
      tag: 'el-slider',
      node: `<el-slider/>`,
    },
    {
      title: '',
      tag: 'el-switch',
      node: `<el-switch/>`,
    },
  ];

  let baseForm = {
    title: '',
    children: [/*{
      title: '',
      node: '<el-form></el-form>',
    },*/ {
      title: '',
      node: '<el-form-item label=""></el-form-item>',
    }].concat(inputs.map((item) => {
      return {
        title: item.title,
        tag({ parentNode, targetNode }) {
          if (!parentNode) return;
          if (parentNode.tag === 'el-row') {
            return 'el-col';
          }
          if (parentNode.tag === 'el-col' && parentNode.isEmpty()) {
            return 'el-form-item';
          }
          if (parentNode.tag === 'el-form' || ( parentNode.tag === 'el-col' && parentNode.isEmpty() ) || ( targetNode?.tag === 'el-form-item' && !targetNode.isEmpty() )) {
            return `el-form-item`;
          }
          return item.tag;
        },
        node({ parentNode, targetNode, dblClick }) {
          if (!parentNode) return;

          const colNode = `<el-col :span="12"><el-form-item label="${ item.title }">${ item.node }</el-form-item></el-col>`;

          if (parentNode.tag === 'el-row') {
            return colNode;
          }

          const formItemNode = `<el-form-item label="${ item.title }">${ item.node }</el-form-item>`;
          if (parentNode.tag === 'el-form' ||
              ( parentNode.tag === 'el-col' && parentNode.isEmpty() ) ||
              ( targetNode?.tag === 'el-form-item' && !targetNode.isEmpty() )) {
            return formItemNode;
          }

          if (dblClick && parentNode.tag === 'el-form-item') {
            if (parentNode.isEmpty()) {
              return item.node;
            }
            return formItemNode;
          }
        },
      };
    })),
  };

  return [base, layout, baseForm];
};
