<template>
  <a-form :label-width="{ style: { width: '100px' } }" :wrapper-col="{ flex: 1 }">
    <a-form-item label="">
      <a-input v-model:value="rule.message" allowClear placeholder=""></a-input>
    </a-form-item>

    <a-form-item label="">
      <a-select v-model:value="rule.trigger" allowClear style="width: 170px">
        <a-select-option value="change"></a-select-option>
        <a-select-option value="blur"></a-select-option>
      </a-select>
    </a-form-item>

    <a-form-item>
      <template #label>
        <a-checkbox v-model:checked="rule.state.required"></a-checkbox>
      </template>
      <a-switch v-model:checked="rule.required" checked-children="" un-checked-children=""/>
    </a-form-item>

    <a-form-item>
      <template #label>
        <a-checkbox v-model:checked="rule.state.type"></a-checkbox>
      </template>
      <a-select v-model:value="rule.type" style="width: 170px" clearable>
        <a-select-option :key="k" :value="k" v-for="(t, k) in types">{{ t }}</a-select-option>
      </a-select>
    </a-form-item>

    <a-form-item>
      <template #label>
        <a-checkbox v-model:checked="rule.state.range"></a-checkbox>
      </template>
      <a-input-number v-model:value="rule.min"/>
      -
      <a-input-number v-model:value="rule.max"/>
    </a-form-item>

    <a-form-item>
      <template #label>
        <a-checkbox v-model:checked="rule.state.len"></a-checkbox>
      </template>
      <a-input-number v-model:value="rule.len"/>
    </a-form-item>

    <a-form-item>
      <template #label>
        <a-checkbox v-model:checked="rule.state.pattern"></a-checkbox>
      </template>
      <VucCodeEditor v-model:value="rule.pattern" inline style="width: 100%"></VucCodeEditor>
    </a-form-item>
  </a-form>
</template>

<script>
import { CodeEditor } from 'vuc3';
import { types } from './utils';

export default {
  name: 'RuleForm',
  components: {
    VucCodeEditor: CodeEditor,
  },
  props: {
    rule: Object,
  },
  data() {
    return {
      types,
    };
  },
  mounted() {
    let t = {
      min: 'range',
      max: 'range',
      len: 'len',
      type: 'type',
      required: 'required',
      pattern: 'pattern',
    };

    for(let key in t) {
      let state = t[key];
      this.$watch(`rule.${ key }`, () => {
        this.rule.state[state] = true;
      });
    }
  },
};
</script>
