import { getSizeEditor } from '../utils';

export default {
  id: 'el-radio-group',
  name: '',
  eventTypes: [
    {
      id: 'change',
      name: '',
    },
  ],
  type: ['input', 'radio'],
  props: {
    modelValue: {
      label: '',
      editors: 'string',
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    size: {
      label: '',
      editors: getSizeEditor(),
    },
    textColor: {
      label: '',
      editors: 'string',
    },
    fill: {
      label: '',
      editors: 'string',
    },
  },

  optionEditor: {
    title: '',
    tag: 'el-radio',
    getData(node) {
      return {
        value: node.getAttrValue('value'),
        label: node.getText(),
      };
    },
    setData(node, data) {
      node.setAttrValue('value', data.value);
      node.setText(data.label);
    },
  },

  vModel: ['modelValue'],

  vucProxyOption: {
    textProxy: false,
  },
};
