export default {
  id: 'el-form',
  name: '',
  eventTypes: [
    {
      id: 'validate',
      name: '',
    },
  ],
  type: ['container', 'form'],
  slots: [
    {
      slot: 'default',
      name: '',
    },
  ],
  props: {
    model: {
      label: '',
      change(attr, old, node) {
        let inputs = node.querySelectorAll('.input');
        inputs.forEach(input => {
          let model = input.getDirective('model');
          if (model) {
            let modelValue = model.getAttrValue();
            let v = modelValue.split('.');
            if (v[0] == old.attrValue) {
              let v1 = [attr.attrValue].concat(v.slice(1)).join('.');
              input.setAttrValue(model.getAttrName(), v1);
            }
          }
        });
      },
    },
    rules: {
      label: '',
    },
    inline: {
      label: '',
      editors: 'boolean',
    },
    disabled: {
      label: '',
      editors: 'boolean',
    },
    labelWidth: {
      label: '',
      editors: 'string',
    },
    size: {
      label: '',
      editors: 'select?@items=medium:|small:|mini:',
    },
    labelPosition: {
      label: '',
      editors: 'select?@items=left:|right:|top:',
    },
    labelSuffix: {
      label: '',
      editors: 'string',
    },
    hideRequiredAsterisk: {
      label: '',
      editors: 'boolean',
    },
    showMessage: {
      label: '',
      editors: 'boolean?defaultValue=true',
    },
    inlineMessage: {
      label: '',
      editors: 'boolean?trueText=&falseText=',
    },
    statusIcon: {
      label: '',
      editors: 'boolean',
    },
    validateOnRuleChange: {
      label: '',
      editors: 'boolean?defaultValue=true',
    },
  },
};
